html, body, div#app, .write_article_wrap { height: 100%; margin: 0 }
ul, p { padding: 0; margin: 0 }
li { list-style: none }
a { text-decoration: none; color: #333 }
.p_add { font-size: 16px; cursor: pointer; padding: 15px 0; padding-left: 20px; margin-bottom: 1px }
.p_add i.i_add { font-style: initial; font-size: 25px; vertical-align: -2px }
.add_wrap { overflow: hidden; height: 80px }
.add_wrap input { border: 0; width: 260px; margin-left: 10px; line-height: 28px; border-radius: 3px; margin-bottom: 8px; font-size: 16px; padding-left: 10px }
.floder a.btn.back_home { width: 243px; line-height: 35px; height: 35px; margin-top: 20px }
.floder .item li { font-size: 16px; padding-left: 14px; line-height: 45px; cursor: pointer; overflow: hidden }
.floder .item li.editor { height: 90px; margin-right: 1px }
.floder .item ul { margin-top: 5px }
.floder .item i.i1 { display: none }
.floder .item li.active i.i1 { display: inherit; float: right; margin-right: 15px }
.floder .item li.active { border-left: 5px solid #ff5548; margin-left: 0; padding-left: 9px }
.floder .item li.active i.i1:hover { color: #ff5548 }
.floder .item li:hover, .floder li.active { background-color: #5a5a5a; position: relative }
.floder .item li.editor input { border: 0; height: 30px; vertical-align: 2px; padding-left: 5px; border-radius: 3px; width: 260px }
.floder { float: left; width: 15%; background-color: #333; color: white; height: 100%; text-align: left; overflow: auto; position: relative }
div.floder-cover { position: absolute; top: 430px; left: 210px; width: 100px; height: 100px; z-index: 999; border-radius: 9px; overflow: hidden; cursor: pointer }
div.floder-cover .background { background-color: #301342; opacity: 0.5; position: absolute; left: 0; top: 0; width: 100%; height: 100%; z-index: 0 }
ul.subitem { position: absolute; z-index: 999; width: 100% }
.subitem i.iconfont { font-size: 36px; line-height: 50px }
.subitem li { float: left; width: 50px; text-align: center; display: block; height: 50px; overflow: hidden; cursor: pointer; color: white }
.subitem li:hover i { color: #ff5548 }
.document { overflow: auto; width: 20%; height: 100%; float: left; text-align: left; border-right: 1px solid #e8e8e8; margin-left: -1px }
.document .i1 { float: left; display: block; width: 19px; height: 26px; background-image: url("../image/1.png"); background-position: 0px 0px; left: 20px; margin-top: 24px; position: absolute }
.document li { min-height: 80px; overflow: hidden; cursor: pointer; border-bottom: 1px solid #eaeaea }
.document a.btn { border-color: #ddd; color: #2f2f2f }
.document .add_wrap input { border-color: #ddd; border: 1px solid #ddd }
.document li.active { border-left: 5px solid #ff5548 }
.document li.active, .document li:hover { background-color: #eaeaea }
.document ul { margin-left: 1px; position: relative }
.document li p.p1 { margin-top: 20px; margin-left: 55px; margin-right: 30px; font-size: 18px }
.document li p.p2 { margin-left: 55px; margin-right: 30px; font-size: 14px; color: #9c9c9c; margin-bottom: 15px }
.document li.active p.p1 { margin-left: 50px }
.document li.active p.p2 { margin-left: 50px }
.article { float: left; width: 65%; height: 100%; overflow: hidden; display: block; background-color: white; text-align: left }
.article textarea { width: 100%; margin-bottom: 0; resize: none; color: #2f2f2f; font-size: 18px; font-weight: normal; line-height: 30px; -webkit-appearance: none; box-sizing: border-box; border: 0; -webkit-highlight: none; outline: none; padding: 30px; padding-bottom: 0; overflow: auto }
.article p.p1 { height: 50px; margin: 0; background-color: #f7f7f7; margin-top: 10px; box-sizing: border-box; overflow: hidden; border-top: 0; border-bottom: 1px; border: 6px solid #808080; border-left: 0; border-right: 0; border-top: 2px solid #dad5d5 }
.article input.i1 { margin-top: 10px; margin-left: 30px; width: 70%; border: 0; outline: none; font-size: 36px; box-sizing: border-box }
.article p.p1 .i1 { margin-top: 5px; display: block; float: right; margin-right: 30px; font-size: 26px; color: #616161; cursor: pointer }
.article p.p1 .i1:hover { color: #ff5548 }


